import { Component } from "@angular/core";
import { ModalController, NavParams } from '@ionic/angular';
import { PsCommonSettings } from 'src/app/commonSRC/psServices/models/ps-common.settings';
import { PsCommonService } from "src/app/commonSRC/psServices/ps-common/ps-common.service";
import { ActionDetailsObject } from "../ps-complex-widget-dropdowns/ps-complex-widget-dropdowns.component.interface";
import { PsBaseActionComponent } from './../../../../commonSRC/psComponents/ps-base/ps-base-action/ps-base-action.component';
import { ConstantCommon } from './../../../../commonSRC/psServices/models/common-constant';
import { CommonUtils } from './../../../../commonSRC/psServices/models/common-utils';
import { IOptionsPsLabel, IOptionsPsLabelHeader, IPsMaskOptions } from './../../../../commonSRC/psServices/models/ps-common-interface';
import { IOptionsPsComplexRewardDetailsComponent } from './ps-complex-reward-details.component.interface';

@Component({
    selector: 'ps-complex-reward-details',
    templateUrl: './ps-complex-reward-details.component.html'
})
export class PsComplexRewardDetailsComponent extends PsBaseActionComponent {
    rewardData: IOptionsPsComplexRewardDetailsComponent = {};
    rewardAction: ActionDetailsObject;
    merchantLabelOptions: IOptionsPsLabel = {
        labelKey: 'merchant_name_key'
    };
    validityLabelOptions: IOptionsPsLabel = {
        labelKey: 'reward_validity_key'
    };
    headerOptions: IOptionsPsLabelHeader = {
        labelKey: 'reward_description_key'
    }
    maskOptions: IPsMaskOptions = {
        groupSeparator: PsCommonSettings.THOUSANDS_SEPARATOR,
        alias: 'currency',
        autoGroup: true,
        placeholder: '0',
        greedy: false,
        digits: ConstantCommon.DEFAULT_DECIMAL_DIGITS
    };
    constructor(public navParams: NavParams, public commonProv: PsCommonService, private modalCtrl: ModalController) {
        super(commonProv, commonProv.logger);
    }

    ngOnInit() {
        super.ngOnInit();
        this.rewardData = this.navParams.get('data');
        this.rewardAction = this.navParams.get('actions')[0];
        if (this.rewardData.pathNumberOfPoints) {
            this.rewardData.pathNumberOfPoints = CommonUtils.applyMaskFormat(+this.rewardData.pathNumberOfPoints, this.maskOptions) + ' ' + this.commonProv.translate('points_key');
        }
        this.rewardData.pathImageUrl = this.rewardData.pathImageUrl ? this.rewardData.pathImageUrl : 'noImage.jpg';
    }

    onRedeemClicked(data) {
        this.rewardAction.action(data);
        this.modalCtrl.dismiss();
    }
}