import { Component, OnInit } from '@angular/core';
import { FormGroup } from '@angular/forms';
import { ConstantCommon } from 'src/app/commonSRC/psServices/models/common-constant';
import { PsCommonSettings } from 'src/app/commonSRC/psServices/models/ps-common.settings';
import { PsComplexRewardDetailsComponent } from './../../commonBussinessSRC/psComponents/ps-complex-components/ps-complex-reward-details/ps-complex-reward-details.component';
import { IOptionsPsComplexWidgetDropdownsExposed } from './../../commonBussinessSRC/psComponents/ps-complex-components/ps-complex-widget-dropdowns/ps-complex-widget-dropdowns.component.interface';
import { CommonBussinessConstant } from './../../commonBussinessSRC/psServices/models/ps-common-bussiness-constant';
import { OmniPullService } from './../../commonBussinessSRC/psServices/omni-common/omni-pull.service';
import { OmniPushService } from './../../commonBussinessSRC/psServices/omni-common/omni-push.service';
import { LoggerService } from './../../commonSRC/psServices/logger/logger.service';
import { IOptionsPsTemplateView, IPsMaskOptions } from './../../commonSRC/psServices/models/ps-common-interface';
import { PsCommonService } from './../../commonSRC/psServices/ps-common/ps-common.service';
import { OmniBasePage } from './../omni-base/omni-base.page';
@Component({
    selector: 'rewards-catalogue',
    templateUrl: './rewards-catalogue.page.html'
})
export class RewardsCataloguePage extends OmniBasePage implements OnInit {
    formGroup = new FormGroup({});
    rewardsCatalogueVO = {};
    showCategories = false;
    maskOptions: IPsMaskOptions = {
        groupSeparator: PsCommonSettings.THOUSANDS_SEPARATOR,
        alias: 'currency',
        autoGroup: true,
        placeholder: '0',
        greedy: false,
        digits: ConstantCommon.DEFAULT_DECIMAL_DIGITS
    };
    templateViewOptions: IOptionsPsTemplateView = {
        group: this.formGroup,
        disableLoading: true
    };
    widgetDropdownsOptions: IOptionsPsComplexWidgetDropdownsExposed = {
        group: this.formGroup,
        requestObject: this.rewardsCatalogueVO,
        detailsObject: {
            detailsModal: PsComplexRewardDetailsComponent,
            detailsModalClass: 'reward-details-modal',
            detailsModalId: 'ps-complex-reward-details-modal',
            detailsActionsList: [{ label: 'redeem_now_key', action: (data) => this.applyRedemption(data) }]
        },
        dropdowns: [
            {
                group: this.formGroup,
                fcName: 'categories',
                labelKey: 'categories_key',
                listOfOptions: [],
                asyncURL: CommonBussinessConstant.RETURN_REWARDS_CATEGORIES_URL,
                serviceRequestObj: { submitType: CommonBussinessConstant.RETURN_CATEGORIES_SUBMIT_TYPE, operId: CommonBussinessConstant.REWARDS_CATALOGUE_OPER },
                mapResponseProperties: {
                    itemValue: 'pathRewardCategoryId',
                    description: 'pathName',
                    imageName: 'pathImageUrl',
                    fallBackImageName: CommonBussinessConstant.NO_IMAGE_FOUND_FALLBACK
                },
                enablePagination: false,
                requestObject: this.rewardsCatalogueVO,
                notSelectable: true
            },
            {
                group: this.formGroup,
                fcName: 'rewards',
                labelKey: 'rewards_key',
                listOfOptions: [],
                asyncURL: CommonBussinessConstant.RETURN_REWARDS_URL,
                serviceRequestObj: { submitType: CommonBussinessConstant.RETURN_REWARDS_SUBMIT_TYPE, operId: CommonBussinessConstant.REWARDS_CATALOGUE_OPER },
                mapResponseProperties: {
                    itemValue: 'pathRewardCategoryIdValue',
                    description: 'pathName',
                    imageName: 'pathImageUrl',
                    fallBackImageName: CommonBussinessConstant.NO_IMAGE_FOUND_FALLBACK,
                    additionalDescList: [
                        { propNames: ['pathMerchantRewardsCatalogId'] },
                        { propNames: ['pathExpiryDate'] },
                        { propNames: ['pathNumberOfPoints'], maskOptions: this.maskOptions, concatString: 'points_key' }
                    ],
                    additionalActionList: [{ labelKey: 'redeem_now_key', group: this.formGroup, handler: (data) => this.applyRedemption(data) }]
                },
                enablePagination: false,
                requestObject: this.rewardsCatalogueVO,
                notSelectable: true,
                isMultiDescription: true
            }
        ]
    };
    constructor(public commonService: PsCommonService, public logger: LoggerService,
        public omniPull: OmniPullService, public omniPush: OmniPushService) {
        super();
    }
    ngOnInit() {
        super.init();
        this.baseFormGroup = this.formGroup;
        this.commonProv.setFormData(this.formGroup, this.rewardsCatalogueVO);
    }

    applyRedemption(data) {
        if (data) {
            const parameter = {
                submitType: CommonBussinessConstant.APPLY_REDEMPTION_SUBMIT_TYPE,
                actionType: 'Submit',
                commonParametersList: { ...data.selectedObj }
            };
            this.commonProv.presentLoading();
            this.omniPush.redeemPoints(parameter).then(() => {
                this.omniPull.getPointsBalance({}).then(() => {
                    this.commonProv.dismissLoading();
                });
            });
        }
    }
}